<template>
  <div class="textColumn">
    <div class="h3" style="text-align: justify; color: var(--bs-dark)">
      {{ item.title }}
    </div>

    <div style="display: flex; margin: 30px 0; font-size: 0.9rem">
      <div style="margin-right: 30px">{{ item.date }}</div>
      <div style="margin-right: 30px; text-align: left">{{ item.author }}</div>

      <div>
        <a :href="item.newsLinks" target="_blank">{{ $t("newsLink") }}</a>
      </div>
    </div>

    <!-- content -->
    <div
      v-for="newsContent in item.content"
      :key="newsContent"
      :class="{
        newsList: newsContent.type == 'list',
        newsUl: newsContent.type == 'ul',
        newsSubtitle: newsContent.type == 'subtitle',
        newsParagraph: newsContent.type == 'paragraph',
        newsCaption: newsContent.type == 'caption',
        newsForm: newsContent.type == 'form',
      }"
    >
      <template v-if="newsContent.type == 'img'">
        <img class="newsImg" :src="newsContent.content" />
      </template>

      <div
        class="ratio ratio-16x9 videoStyle"
        v-else-if="newsContent.type == 'video'"
      >
        <iframe
          :src="newsContent.content"
          class="embed-responsive-item"
          allow="autoplay;"
          allowfullscreen
        ></iframe>
      </div>

      <template v-else>
        {{ newsContent.content }}
      </template>
    </div>
    <!-- end of content -->

    <!-- Pagination -->
    <div class="setRow">
      <div>
        <router-link
          :class="{ disabled: isPreBtnDisabled == true }"
          :to="{ name: 'news', params: { id: prevPage } }"
          style="margin: 10px"
          >{{ $t("previous") }}</router-link
        >
      </div>

      <div>
        <router-link to="/newsList" style="margin: 10px">{{
          $t("back")
        }}</router-link>
      </div>

      <div>
        <router-link
          :class="{ disabled: isNextBtnDisabled == true }"
          :to="{ name: 'news', params: { id: nextPage } }"
          style="margin: 10px"
          >{{ $t("next") }}</router-link
        >
      </div>
    </div>
    <!-- end of Pagination -->
  </div>
</template>

<style scoped>
a {
  text-decoration: none;
  color: var(--bs-secondary);
  text-align: left;
}
a:hover {
  color: var(--bs-primary);
}

a.disabled {
  pointer-events: none;
  opacity: 30%;
}
.setRow {
  display: flex;
  justify-content: space-between;
  margin: 50px auto;
  width: 50%;
}

.newsSubtitle {
  font-size: 1.3rem;
  font-weight: bold;
  line-height: 2.2rem;
  margin: 30px 0 0px 0;
  color: var(--bs-primary);
  text-align: justify;
}
.newsParagraph {
  font-size: 1rem;
  line-height: 1.4rem;
  margin: 20px 0;
  text-align: justify;
}

.newsUl {
  display: list-item;
  line-height: 2rem;
  list-style-position: inside;
  text-align: justify;
}

.newsList {
  display: list-item;
  line-height: 2rem;
  list-style-position: inside;
  list-style-type: decimal;
  text-align: justify;
}

.newsImg {
  max-width: 80%;
  margin: 30px auto;
  display: flex;
}

.newsCaption {
  max-width: 80%;
  font-size: 0.8rem;
  margin: -20px auto 30px auto;
  text-align: start;
}

.newsForm {
  max-width: 80%;
  margin: 30px auto -25px auto;
  text-align: center;
}

.videoStyle {
  width: 80%;
  margin: 30px auto;
}
@media (max-width: 500px) {
  .newsImg {
    max-width: 100%;
  }
  .newsCaption {
    max-width: 100%;
  }

  .videoStyle {
    width: 100%;
  }
  .setRow {
    width: 100%;
  }
}
</style>

<script>
import json from "@/assets/press/news.json";
import { useRoute } from "vue-router";
import { ref } from "vue";
export default {
  setup() {
    const route = useRoute();
    const item = json[route.params.id];
    // console.log(item);
    const currentPage = Number(route.params.id);

    const isPreBtnDisabled = ref(true);
    const isNextBtnDisabled = ref(false);
    const prevPage = currentPage - 1;
    const nextPage = currentPage + 1;

    const countPage = () => {
      // console.log("jsonL", json.length, "nextPage", nextPage);
      if (prevPage >= 0) {
        isPreBtnDisabled.value = false;
      } else {
        isPreBtnDisabled.value = true;
      }
      if (nextPage == json.length) {
        isNextBtnDisabled.value = true;
      } else {
        isNextBtnDisabled.value = false;
      }
    };
    countPage();
    return {
      item,
      prevPage,
      nextPage,
      currentPage,
      isPreBtnDisabled,
      isNextBtnDisabled,
      countPage,
    };
  },
};
</script>
